/*===== Separaciones =====*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Ubuntu", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #181A1B;
  color: #E8E6E3;
  overflow-x: hidden;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Ubuntu", sans-serif;
  margin: 2.5rem;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.apis {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.apis a {
  color: #E8E6E3;
  font-size: 2rem;
}
.apis a:hover {
  color: #d1cdc7;
  border-bottom: 1px solid #E8E6E3;
}

/*   border radius */
.nav {
  padding: 1rem;
  width: 100vw;
  box-shadow: 0 0 10px black;
}
.nav .contenedor {
  display: flex;
  justify-content: space-between;
}
.nav .contenedor .nav__listlogo, .nav .contenedor a {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.nav .contenedor .nav__listlogo img, .nav .contenedor a img {
  width: 5rem;
}
.nav .contenedor .nav__listlogo h3, .nav .contenedor a h3 {
  font-size: 2rem;
  margin: 0;
}
.nav a {
  color: #fff;
}
.nav__list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 2rem;
  margin: 0 2rem;
}
.nav__listitem {
  list-style: none;
  font-weight: bold;
  position: relative;
  padding: 1.5rem 1rem;
  cursor: pointer;
}
.nav__listitem::after {
  content: "";
  width: 0;
  height: 0.3rem;
  border-radius: 0.2rem;
  position: absolute;
  left: 1rem;
  bottom: 0.8rem;
  background-color: #fff;
  transition: width 200ms ease-in;
}
.nav__listitem:hover::after, .nav__listitem:focus::after {
  width: 90%;
}
.nav__listitem:hover ul, .nav__listitem:focus ul {
  opacity: 1;
  visibility: visible;
}
.nav__listitemdrop {
  position: absolute;
  top: 4rem;
  left: -1rem;
  box-shadow: 0 0 10px black;
  background-color: #181a1b;
  border-radius: 0.2rem;
  width: 15rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  opacity: 0;
  visibility: hidden;
  transition: opacity 200ms ease-in-out;
}
.nav__listitemdrop li, .nav__listitemdrop a {
  list-style: none;
  padding: 0.5rem 1rem;
  border-radius: 0.2rem;
  transition: background-color 200ms ease-in-out;
}
.nav__listitemdrop li:hover, .nav__listitemdrop li:focus, .nav__listitemdrop a:hover, .nav__listitemdrop a:focus {
  background-color: #1A1A40;
}